// Slider
$(document).ready(function() {
    $(".trust-slider").slick({
       arrows: false,
       dots: false,
       infinite: true,
       autoplay: true,
       slidesToShow: 5,
       slidesToScroll: 2,
       autoplaySpeed: 0,
       speed: 5000,
       pauseOnFocus: true,
       responsive: [
           {
             breakpoint: 768,
             settings: {
                slidesToShow: 3,
             }
           },
       ]
    })
})